export default function resFilter(err, res, msg) {
  console.log('resUtil',err, res, msg)
  if(res) {
    console.log(res)
    if(res.code === 1) {
      
      return true
    } else if(res.code === 0) {
      uni.showToast({
        title: res.msg,
        icon: 'none'
      })
      return false
    } else if(res.code === -1) {
      uni.showToast({
        title: res.msg,
        icon: 'none'
      })
      return false
    }
  }
}